@import "../core/variables";

.mob {
  display: block;
}

.desk {
  display: none;
}

@media (min-width: 768px) {
  .mob {
    display: none;
  }

  .desk {
    display: block;
  }
}

header {
  margin-top: 40px;

  .navbar-toggler {
    border: none;
    outline: none;
  }

  .phone {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;

    .cst-i {
      margin-right: 16px;
    }

    @media (min-width: 768px) {
      justify-content: flex-end;
    }
  }

  .nav-item {
    &.active {
      position: relative;
      display: flex;

      &:after {
        content: '';
        position: absolute;
        height: 7px;
        width: 7px;
        background-color: $main-green;
        bottom: 0px;
        left: 0;
        right: 0;
        margin: auto;
        border-radius: 50%;
      }
    }

  }

  .navigation-list-item {
    &.active {
      margin-bottom: 10px;
      position: relative;
      display: flex;

      &:after {
        content: '';
        position: absolute;
        height: 6px;
        width: 6px;
        background-color: $main-green;
        bottom: 18px;
        left: 0;
        right: 0;
        margin: auto;
        border-radius: 50%;
      }
    }

  }
}

.logo {
  margin: 0 auto;

  @media (min-width: 992px) {
    margin: 0;
  }
}

.nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .nav-item {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    margin: 0 29px;
  }

  @media (min-width: 992px) {
    flex-direction: row;
  }
}


.mob {

  .collapse {

    .navigation-list {
      width: 100%;
      background-color: #0e0e0e;
      padding: 10px 26px 30px 26px;
      display: flex;
      flex-direction: column;

      .navigation-list-item {
        padding: 30px 0;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top: 1px solid #6a6a6a;;
        color: #ffffff;


        &:first-child {
          border-top: none !important;
        }
      }

      .phone {
        a, span {
          color: $main-green;
        }

        margin: 30px;
      }
    }
  }
}

.lead-select {
  background-color: #fdfdfd;

  .card {
    border: none;

    .card-header {
      padding: 0;
      background-color: unset;
      position: relative;
      border: none;

      &.collapsed {

        border-bottom: 1px solid #efefef;

        &:after {
          content: '';
          position: absolute;
          height: 17px;
          width: 4px;
          background-color: #b1b1b1;
          right: 26px;
          top: 0;
          bottom: 0;
          margin: auto;
          border-radius: 20px;
        }

        &:before {
          background-color: #b1b1b1;
        }
      }

      &:before {
        content: '';
        position: absolute;
        height: 4px;
        width: 17px;
        background-color: $main-green;
        right: 20px;
        top: 0;
        bottom: 0;
        margin: auto;
        border-radius: 20px;
      }

      &:after {
        background-color: $main-green;
      }

      .checkbox-gr {
        margin-bottom: 0;

        label {
          padding: .75rem 1.25rem;

        }
      }
    }

    .card-body {
      padding-left: 45px;
      padding-bottom: 0;
      padding-top: 10px;
    }
  }
}

.checkbox-gr {
  display: flex;
  align-items: center;
  margin-bottom: 14px;
  cursor: pointer;

  label {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    font-size: 18px;
    font-weight: 500;
    color: #a5a5a5;
    flex-grow: 1;
    cursor: pointer;

    span {
      margin-left: auto;
    }
  }

  input[type='checkbox'] {
    visibility: hidden;
    position: relative;
    height: 16px;
    width: 16px;
    margin-right: 12px;

    &:before {
      content: '';
      visibility: visible;
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      height: 16px;
      width: 16px;
      border-radius: 1px;
      border: solid 1px #a5a5a5;
      cursor: pointer;
    }

    &:checked {
      &:before {
        content: '';
        visibility: visible;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        height: 16px;
        width: 16px;
        border-radius: 1px;
        border-color: $main-green;
        background-color: $main-green;
        cursor: pointer;
      }

      &:after {
        content: '';
        visibility: visible;
        position: absolute;
        left: 7px;
        top: 3px;
        height: 9px;
        width: 4px;
        border-right: 1px solid white;
        border-bottom: 1px solid white;
        transform: rotate(45deg);
      }

    }


  }

  &.sub {
    label {
      font-size: 16px;
      font-weight: 400;
    }

  }
}

.functionality-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  .card {
    padding: 46px 35px 36px;
    border-radius: 4px;
    box-shadow: 0 4px 22px 0 rgba(0, 0, 0, 0.05);
    background-color: #ffffff;
    width: 100%;
    max-width: 360px;
    margin: 15px;

    .cst-i {
      text-align: center;
      margin-top: 15px;
      font-size: 40px;
    }

    &:hover {
      border-color: $main-green;

      .cst-i {
        color: $main-green;
      }
    }


  }

  @media (min-width: 768px) {
    .card {
      &:last-child {
        margin-right: 0;
      }

      &:first-child {
        margin-left: 0;
      }
    }
    flex-wrap: nowrap;
  }
}

.card-shadow-box {
  border-radius: 4px;
  box-shadow: 0 4px 22px 0 rgba(0, 0, 0, 0.05);
}

.scroll-down-btn {
  border-radius: 50%;
  border: 1px solid $main-green;
  height: 50px;
  width: 50px;
  position: relative;
  display: flex;
  cursor: pointer;

  &:after {
    content: '';
    position: absolute;
    top: 12px;
    left: 0;
    right: 0;
    margin: auto;
    border-right: 1.5px solid $main-green;
    border-bottom: 1.5px solid $main-green;
    height: 20px;
    width: 20px;
    transform: rotate(45deg);
  }
}

.previous-selection {
  padding: 50px 36px;
}

.target-group {
  padding: 20px;
  background-color: $main-green;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 22px;
}

.radio-btn {
  visibility: hidden;
  position: relative;
  display: flex;
  height: 16px;
  width: 16px;
  cursor: pointer;

  &:before {
    content: '';
    visibility: visible;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    border: 1px solid #a5a5a5;
    border-radius: 50%;
    height: 16px;
    width: 16px;
    display: flex;
  }

  &:after {
    content: '';
    visibility: hidden;
    position: absolute;
    left: 2px;
    top: 0;
    bottom: 0;
    margin: auto;
    border-radius: 50%;
    background-color: $main-green;
    height: 11px;
    width: 11px;
    display: flex;
  }

  &:checked {
    &:after {
      visibility: visible;
    }

    &:before {
      border-color: $main-green;
    }
  }
}

.scoreboard {
  width: 100%;
  padding: 11px 20px;
  border-radius: 4px;
  background-color: #f8f8f8;
}

.customers-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  .customer-card {
    width: 100%;
    max-width: 262px;
    height: 262px;
    border-radius: 10px;
    border: 2px solid #f8f8f8;
    margin: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      border-color: $main-green;
    }
  }
}

.figures {
  padding-top: 70px;
  background-image: linear-gradient(to bottom, #fdfdfd, #ffffff);

  .figures-cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .figures-card {
      box-shadow: 0 4px 26px 0 rgba(0, 0, 0, 0.04);
      background-color: #ffffff;
      width: 100%;
      max-width: 360px;
      padding: 44px 32px;
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      align-items: center;
      border-bottom: 2px solid #ffffff;
      border-radius: 54px 0 0 0;
      margin: 15px;

      .icon-frame {
        height: 80px;
        width: 80px;
        margin-bottom: 22px;
        border-radius: 10px;
        border: solid 1px $main-green;
        color: $main-green;
        display: flex;
        justify-content: center;
        align-items: center;

        .cst-i {
          font-size: 40px;
        }

        .los-i {
          font-size: 51px;
        }
      }

      &:hover {
        border-bottom: 2px solid $main-green;

        .icon-frame {
          background-color: $main-green;
          color: white;
        }
      }
    }

    @media (min-width: 992px) {
      flex-wrap: nowrap;

      .figures-card {
        &:last-child {
          margin-right: 0;
        }

        &:first-child {
          margin-left: 0;
        }
      }
    }
  }
}

.service-card {
  width: 100%;
  margin-top: 42px;
  max-width: 270px;
  margin-right: auto;
  margin-left: auto;

  .card-banner {
    height: 200px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 24px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    .cst-i {
      color: white;
      font-size: 30px;
      margin-top: 14px;
    }
  }

  ul {
    margin-top: 12px;
    padding-left: 16px;
    margin-bottom: 22px;

    li {
      margin-bottom: 16px;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: -16px;
        bottom: 0;
        margin: auto;
        height: 6px;
        width: 6px;
        border-radius: 50%;
        background-color: #838383;
      }
    }
  }

  .card-read-more {
    color: $main-green;
    font-size: 14px;
    font-weight: 600;
    width: 100px;
    position: relative;
    display: flex;

    &:after {
      content: '';
      position: absolute;
      height: 8px;
      width: 8px;
      right: 5px;
      top: 9px;
      border-top: 1px solid $main-green;
      border-left: 1px solid $main-green;
      transform: rotate(45deg);
    }

    &.collapsed {
      color: $main-black;
      &:after {
        top: 5px;
        border-top: 1px solid $main-black;
        border-left: 1px solid $main-black;
        transform: rotate(225deg);
      }
    }

  }

}

.price-overview {
  margin-top: 80px;
  padding: 50px 30px;
  box-shadow: 0 4px 22px 0 rgba(0, 0, 0, 0.05);
  background-color: #ffffff;

  .table {
    margin-top: 22px;
    text-align: end;

    th, td {
      padding-right: 0;
      padding-left: 0;
      border-top: none;
      border-bottom: none;

      &:first-child {
        text-align: start;
      }
    }

    tr:last-child td {
      border-top: 1px solid #f0f0f0;
    }
  }

}

.questions {
  height: 100%;
  margin-top: 80px;
  display: flex;
  justify-content: center;
  padding: 0 55px;
  flex-direction: column;
}

footer {
  background-color: #31373e;
  padding-top: 60px;

  ul li {
    margin-bottom: 15px;
  }

  .morespace li {
    margin-bottom: 26px;
  }

  .icon-facebook, .icon-instagram, .icon-linkedin {
    font-size: 22px;
    cursor: pointer;
    color: white;
  }

  .icon-location {
    font-size: 20px;
  }

  .icon-envelope {
    font-size: 15px;
  }

}

.mobile-footer {
  padding-bottom: 40px;

  .accordion {
    margin-top: 25px;
    padding: 0 26px;
  }

  .card {
    background-color: #31373e;
    border: none;
    border-bottom: 1px solid #6a6a6a;
    padding: 13px 0;
    border-radius: 0;

    .card-header {
      background-color: #31373e;
      border: none;
      padding: 0;

      span {
        color: $main-green;
        height: 100%;
        width: 100%;
        position: relative;
        display: flex;

        &:after {
          content: '';
          position: absolute;
          height: 10px;
          width: 10px;
          right: 5px;
          top: 9px;
          border-top: 1px solid $main-green;
          border-left: 1px solid $main-green;
          transform: rotate(45deg);
        }
      }

      &.collapsed {
        color: white;

        span {
          color: white;

          &:after {
            border-top: 1px solid white;
            border-left: 1px solid white;
            transform: rotate(225deg);
          }
        }
      }


    }

    .card-body {
      padding: 10px 0;
    }

    &:last-child {
      border-bottom: none;
    }
  }
}

.form-modal {

  .modal-body {
    padding: 30px 45px;
    @media (min-width: 768px) {
      padding: 60px 98px;
    }
  }


  .form-group {
    margin-bottom: 20px;

    select, input {
      background-color: #f8f8f8;
      border: none;
      border-radius: 4px;
      width: 100%;
      height: 36px;
    }
  }
}


.youtube-frame {
  min-height: 340px;
  height: auto;
  width: 100%;
}


input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type=range]:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}

input[type=range]::-webkit-scrollbar-thumb {

}

input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  /* Hides the slider so custom styles can be added */
  background: transparent;
  border-color: transparent;
  color: transparent;
}

/* Special styling for WebKit/Blink */
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: white;
  border: 5px solid $main-green;
  cursor: pointer;
  position: relative;
}

/* All the same stuff for Firefox */
input[type=range]::-moz-range-thumb {
  border-radius: 50%;
  background: white;
  border: 5px solid $main-green;
  height: 16px;
  width: 16px;
  cursor: pointer;
}

/* All the same stuff for IE */
input[type=range]::-ms-thumb {
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: white;
  border: 5px solid $main-green;
  cursor: pointer;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: #d8d8d8;
  border-radius: 1.3px;
  display: -webkit-flex;
  -webkit-align-items: center;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: #d8d8d8;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: #d8d8d8;
  border-radius: 1.3px;
  display: -moz-box;
  -moz-box-align: center;
}

input[type=range]::-ms-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

.check-btn {
  display: flex;
  align-items: center;
  justify-content: center;

  input[type='checkbox'] {
    display: none;
  }

}
