@import "variables";

@font-face {
	font-family: 'icomoon';
	src: url('../../fonts/icomoon.eot');
	src: url('../../fonts/icomoon.eot') format('embedded-opentype'),
	url('../../fonts/icomoon.ttf') format('truetype'),
	url('../../fonts/icomoon.woff') format('woff'),
	url('../../fonts/icomoon.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

html, body {
	margin: 0;
	width: 100%;
	height: 100%;
	font-family: 'IBM Plex Sans', sans-serif !important;
  	-webkit-background-clip: padding-box;
  	color: #000000;
  	font-size: 14px;
}

button {
	outline: none;
	border: none;
	user-select: none;
}

ul {
	margin: 0;
	padding: 0;
	li {
    display: block;
	list-style: none;
	}
}

a, a:visited,a:hover,a:focus,a:active {
	text-decoration: none;
}

input:focus {
	outline: none;
}

label {
	display: block;
  	text-align: left;
}

strong {
  	font-weight: bold;
}

p {
	margin: 0px;
}

.cursor-pointer {
	cursor: pointer;
}

.outline-non {
	outline: none !important;
}

.btn-success {
	background-color: $main-green;
	border-color: $main-green;
	height: 44px;
	width: 164px;
	border-radius: 4px;

	&:hover {
		background-color: $main-green !important;
		border-color: $main-green !important;
	}

	&:active {
		background-color: $main-green !important;
		border-color: $main-green !important;
	}
}

.btn-outline-success {
	border-color: $main-green;
	color: $main-green;
	height: 44px;
	width: 164px;
	border-radius: 4px;
	font-weight: 700;

	&:hover {
		background-color: $main-green !important;
		border-color: $main-green !important;

	}

	&:active {
		background-color: $main-green !important;
		border-color: $main-green !important;

	}
}

@font-face {
	font-family: 'icomoon';
	src:  url('../../fonts/icomoon.eot?uvns89');
	src:  url('../../fonts/icomoon.eot?uvns89#iefix') format('embedded-opentype'),
	url('../../fonts/icomoon.ttf?uvns89') format('truetype'),
	url('../../fonts/icomoon.woff?uvns89') format('woff'),
	url('../../fonts/icomoon.svg?uvns89#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

